<template>
  <div>
    <!-- 面包屑 -->
    <!-- <header-breadcrumb :breadData="$route.matched" /> -->
    <nav-button>
        <el-button-group>
          <nav-premisson-button :comp="comp" :buttonData="buttonData"></nav-premisson-button>
        </el-button-group>
        <el-button-group>
          <!-- <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增</el-button> -->
          <!-- <el-button icon="fdddfont icon-delete" :disabled="!selectRow.length" @click="batchRemove">删除</el-button> -->
          <!-- <el-button icon="fdddfont icon-shujudaoru" @click="exportsFile">导入</el-button> -->
        </el-button-group>
      </nav-button>
      <!-- 查询 -->
      <common-wrapper>
        <bus-management-search class="search" @search="queryList" @rest="rest"/>
      </common-wrapper>
      <!-- 车辆列表 -->
      <common-wrapper>
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="roleTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="tableData"
              @selection-change="selected => selectRow = selected">
                <el-table-column type="selection" min-width="30"></el-table-column>
                <el-table-column type="index" :index="indexMethod" label="序号" min-width="50"></el-table-column>
                <el-table-column  label="车辆自编号" min-width="100">
                  <template slot-scope="scope">{{scope.row.busSelfCode || '' || null}}</template>
                </el-table-column>
                <el-table-column prop="busPlateNumber" label="车牌号" min-width="120"></el-table-column>
                <el-table-column prop="lineName" label="所属线路" min-width="100"></el-table-column>
                <el-table-column prop="orgName" label="所属机构" min-width="140"></el-table-column>
                <el-table-column prop="devCode" label="设备编号" min-width="140">
                   <template slot-scope="scope">{{scope.row.devCode || '' || null}}</template>
                </el-table-column>
                 <el-table-column  label="启禁状态" min-width="70">
                  <template slot-scope="scope">
                    <el-button  round @click="validBus(scope.$index, scope.row)">{{scope.row.busIsvalid === '0' ? '禁用':'启用'}}</el-button>
                  </template>
                </el-table-column>
                <el-table-column prop="busUpdateTime" label="更新时间" :formatter="formatCreateDate" min-width="180"></el-table-column>
                <el-table-column prop="busUpdateUser" label="更新人" min-width="100">
                  <template slot-scope="scope">{{scope.row.busUpdateUser || '' || null}}</template>
                </el-table-column>

                <el-table-column label="操作" min-width="180">
                  <template slot-scope="scope">
                    <el-tooltip class="item" effect="light" content="编辑"  placement="bottom">
                        <el-button size="mini" round icon="fdddfont icon-modify" @click.stop="handleEdit(scope.$index, scope.row)"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="删除"  placement="bottom">
                        <el-button size="mini" round icon="fdddfont icon-delete" @click.stop="handleDel(scope.$index, scope.row)"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
      <!-- 因为需要时时获取设备编号,所以将这两个模块分开 -->
      <!-- 车辆新增 -->
      <bus-create
        :show.sync="isCreate"
        @updateList="queryList"
      />
      <!-- 车辆修改 -->
      <bus-update
        :show.sync="isEdit"
        :roleData.sync="editRole"
        @updateList="queryList"
      />
       <!-- 导入文件 -->
      <upload-file
         :showUpload.sync="showUpload"
         :uploadFileUrl="uploadFileUrl"
         :limit="limit"
         :size="size"
         :acceptType="acceptType"
         :pojo="pojo"
         :isShowDown="isShowDown"
         :isHasToken="isHasToken"
        @complete="getUploadData"/>
      <!-- 导入文件表格 -->
      <upload-file-table  :showUploadTable.sync="showUploadTable" :pojo="pojo" :uploadData.sync="uploadData" @updateList="queryList" v-if="uploadData.data"/>
  </div>
</template>
<script>
/**
* Bus Management Module
* ----------------------
* Author: dengshijun
* Date: 2018.05.24
*/
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'

import BusManagementSearch from './Components/Search'
import BusUpdate from './Components/BusUpdate'
import BusCreate from './Components/BusCreate'
// 导入文件模块
import UploadFile from '../../Common/Upload'
import UploadFileTable from '../../Common/UploadTable'
import UploadLog from '../../Common/UploadLog'
// api
import { buspageSearch, deleteBus, isvalidBus } from '@/api/management'
export default {
  name: 'BusManagement',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      editRole: {}, // 需要编辑的路线对象
      isEdit: false, // 编辑窗口打开状态
      isListLoading: false, // 列表加载
      isCreate: false, // 新增窗口打开状态
      // 导入需要传入的变量
      showUpload: false, // 导入窗口打开状态 ====>必填
      uploadFileUrl: '/excel/uploadFile', // 文件上传的url ====>必填
      limit: 1, // 上传文件的个数 默认为1
      size: 0.5, // 上传文件总的最大的容量（MB）默认为0.5
      acceptType: '.xls', // 上传文本格式  多个为'.xls,.png,.mp4' 默认为.xls
      pojo: 'TBus', // 上传的额外参数（车辆导入）===> (非必填) 可以是字符串，数字，对象 在上传模块接受到的额外参数将以对象形式上传 {pojo: this.pojo}
      isShowDown: true, // 是否显示下载模板 ===> (非必填)
      isHasToken: true, // 是否需要上传token ===> (非必填)
      // 导入完成后的表格模块变量
      showUploadTable: false, // 导入列表打开状态
      uploadData: {}, // 批量导入返回的数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  activated () {
    this.$refs.roleTable.doLayout()
  },
  mounted () {
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取列表
    getRoles (params) {
      this.isListLoading = true
      buspageSearch(params).then(res => {
        this.tableData = res.list
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表日期格式化
    formatCreateDate (row) {
      if (row.busUpdateTime) {
        return moment(row.busUpdateTime).utcOffset(8).format('YYYY-MM-DD')
      }
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    // 显示新增界面
    handleAdd () {
      this.isCreate = true
    },
    // 显示编辑界面
    handleEdit (index, row) {
      this.editRole = row
      this.isEdit = true
    },
    // 删除的接口方法
    deleteBuses (lineIds) {
      return deleteBus(lineIds).then(res => {
        if (!res.error) {
          this.$message({
            message: '删除成功',
            type: 'success'
          })
          this.queryList()
        }
      })
    },
    // 批量删除
    batchRemove () {
      this.$confirm('确认删除选中的车辆吗？', '提示', {
        type: 'warning'
      }).then(() => {
        let busIds = []
        this.selectRow.map(row => busIds.push(row.busUuid))
        this.isListLoading = true
        this.deleteBuses(busIds).then(() => {
          this.isListLoading = false
        }).catch(() => {})
      })
    },
    // 单行删除
    handleDel (index, row) {
      this.$confirm('确认删除该车辆吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.deleteBuses([row.busUuid])
      }).catch(() => {})
    },
    // 车辆启用禁用
    validBus (index, row) {
      const validStatus = row.busIsvalid === '0' ? '启用' : '禁用'
      this.$confirm(`确认状态修改为${validStatus}?`, '提示', {
        type: 'warning'
      }).then(() => {
        const params = {
          busUuid: row.busUuid,
          busIsvalid: row.busIsvalid
        }
        isvalidBus(params).then(res => {
          console.log(res)
          this.queryList()
        })
      }).catch(() => {})
    },
    // 批量导入
    exportsFile () {
      this.showUpload = true
    },
    // 批量导入数据
    getUploadData (params) {
      setTimeout(() => {
        this.showUploadTable = true
      }, 500)
      let promise = new Promise(function (resolve, reject) {
        resolve(params)
      })
      promise.then(res => {
        this.uploadData = res
      })
    },
    // 导入查看log
    exportsLog () {
      this.$nextPage({
        title: `导入日志详情`,
        props: {
          modelName: '车辆信息批量导入'
        },
        component: UploadLog, // 该组件会通过Webpack分开打包,并异步加载
        cache: true
      })
    }
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    BusManagementSearch,
    BusUpdate,
    BusCreate,
    UploadFile,
    UploadFileTable
  }
}
</script>



